// Form fields
import * as Yup from "yup";
import "yup-phone";

export const loginFields = {
  email: "",
  password: "",
};

export const registerFields = {
  name: "",
  phone: "",
  email: "",
  password: "",
  sex: "",
  admin: false,
};

export const adminFields = (currentProduct) => {
  return currentProduct
    ? {
        name: currentProduct.name,
        category: currentProduct.category,
        price: currentProduct.price,
        img: currentProduct.img,
      }
    : {
        name: "",
        category: "",
        price: "",
        img: "",
      };
};

// Validate Scheme
export const LoginSchema = Yup.object().shape({
  email: Yup.string().email(" email").required(" "),
  password: Yup.string()
    .required(" ")
    .min(8, "    8 ")
    .matches(
      /.*[a-z].*/,
      "         "
    )
    .matches(
      /.*[A-Z].*/,
      "         "
    )
    .matches(/.*[0-9].*/, "      "),
});

export const SignupSchema = Yup.object({
  name: Yup.string()
    .required("   ")
    .min(2, "   "),
  phone: Yup.string()
    .required("  ")
    .phone("+7", false, "    +7"),
  email: Yup.string()
    .required("   ")
    .email("  email"),
  password: Yup.string()
    .required(" ")
    .min(8, "    8 ")
    .matches(
      /.*[a-z].*/,
      "         "
    )
    .matches(
      /.*[A-Z].*/,
      "         "
    )
    .matches(/.*[0-9].*/, "      "),
  sex: Yup.string().required("  "),
});

export const AddingOrChangingSchema = Yup.object({
  name: Yup.string()
    .required("   ")
    .min(3, "   "),
  category: Yup.string().required("  "),
  price: Yup.number()
    .required("  ")
    .typeError("   ")
    .min(30, " 30 "),

  img: Yup.string().url("     URL"),
});
